/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: hsl(216deg, 12%, 8%);
  overflow: hidden;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Overpass", sans-serif;
  font-size: 1.8rem;
  font-weight: 300;
}

h1, h2, h3, h4, h5, li {
  font-family: "Overpass", sans-serif;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.hidden {
  display: none;
  overflow: hidden;
}

.mg-top-5 {
  margin-top: 5rem;
}

.mg-top-10 {
  margin-top: 10rem;
}

.textCenter {
  text-align: center;
}

.containerXl {
  width: 95%;
  max-width: 1800px;
  margin: 0 auto;
}

button {
  all: unset;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.rating {
  transition: transform 1s;
  color: hsl(0deg, 0%, 100%);
  height: 40rem;
  width: 80%;
  background-color: hsl(213deg, 19%, 18%);
  margin: 100vh auto;
  padding: 2rem 2rem 0 2rem;
  border-radius: 1rem;
}
.rating__imgc {
  width: 5rem;
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: hsl(213deg, 19%, 18%);
  filter: brightness(110%);
}
.rating__heading {
  color: hsl(0deg, 0%, 100%);
  font-size: 2.5rem;
  padding-left: 1rem;
}
.rating__desc {
  color: hsl(216deg, 12%, 54%);
  font-size: 1.4rem;
  padding-left: 1rem;
}
.rating__btns {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.rating__btns button {
  padding: 1.5rem 2rem;
  background-color: hsl(213deg, 19%, 18%);
  filter: brightness(120%);
  border-radius: 50%;
  font-size: 1.4rem;
  margin-top: 2rem;
  cursor: pointer;
}
.rating__btns button:hover {
  transition: background-color 0.5s;
  background-color: hsl(217deg, 12%, 63%);
}
.rating__input {
  width: 100%;
  margin-top: 4rem;
  padding: 1.5rem 8rem;
  border-radius: 3rem;
  color: hsl(0deg, 0%, 100%);
  background-color: hsl(25deg, 97%, 53%);
  border: none;
  font-family: "Overpass", sans-serif;
  font-size: 1.4rem;
  letter-spacing: 3px;
  cursor: pointer;
}
.rating__input:hover {
  transition: all 0.5s;
  color: hsl(25deg, 97%, 53%);
  background-color: hsl(0deg, 0%, 100%);
}
@media (min-width: 768px) {
  .rating {
    width: 45rem;
  }
}

.answer {
  text-align: center;
  transition: transform 1s;
}
.answer__img {
  margin: 2rem auto;
}
.answer__stars {
  padding: 0.5rem 1rem;
  border-radius: 2rem;
  margin: 0;
  filter: brightness(120%);
  background-color: hsl(213deg, 19%, 18%);
  display: inline-block;
  font-weight: 400;
  color: hsl(25deg, 97%, 53%);
  font-size: 1.4rem;
}